/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* CSS Variables - Design Tokens */
:root {
  /* Breakpoints */
  --mobile-breakpoint: 375px;
  --tablet-breakpoint: 768px;
  --desktop-breakpoint: 1440px;

  /* Colors */
  --color-background: #111111;
  --color-text-primary: #FFFFFF;
  --color-text-secondary: rgba(255, 255, 255, 0.7);
  --color-dark-text-primary: #111111;
  --color-dark-text-secondary: rgba(17, 17, 17, 0.7);
  --color-dark-text-secondary-80: rgba(17, 17, 17, 0.8);
  --color-card-bg: rgba(255, 255, 255, 0.05);
  --color-order-card: #FFFFFF;
  --color-order-count-bg: #F2F4FA;
  --gradient-start: #8F06D1;
  --gradient-middle: #CC18A6;
  --gradient-end: #FC1470;
  --gradient-rewards: linear-gradient(90deg, var(--gradient-start) 0%, var(--gradient-middle) 50%, var(--gradient-end) 100%);

  /* Spacing */
  --space-0: 0px;
  --space-1: 8px;
  --space-2: 16px;
  --space-3: 24px;
  --space-4: 32px;
  --space-5: 40px;

  /* Border Radius */
  --radius-none: 0px;
  --radius-medium: 8px;    /* Used for action buttons in rewards section */
  --radius-large: 16px;    /* Used for cards and containers */
  --radius-pill: 1000px;   /* Used for tags and pills */

  /* Typography */
  --font-family: 'Greycliff CF', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  --font-weight-extra-bold: 800;

  /* Mobile (375px) */
  --font-size-h1: 45px;
  --line-height-h1: 50px;
  --font-size-h2: 36px;
  --line-height-h2: 40px;
  --font-size-h3: 28px;
  --line-height-h3: 32px;
  --font-size-h4: 24px;
  --line-height-h4: 28px;
  --font-size-regular: 16px;
  --line-height-regular: 24px;
  --font-size-small: 14px;
  --line-height-small: 20px;
  --font-size-tiny: 12px;
  --line-height-tiny: 16px;

  /* Card Sizes */
  --reward-card-width-mobile: 311px;
  --reward-card-width-desktop: 300px;
}

@media (min-width: 1440px) {
  :root {
    --font-size-h1: 64px;
    --line-height-h1: 70px;
    --font-size-h2: 40px;
    --line-height-h2: 48px;
  }
}

/* Base Styles */
body {
  font-family: var(--font-family);
  font-weight: var(--font-weight-medium);
  background-color: var(--color-background);
  color: var(--color-text-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  width: 100%;
  overflow-x: hidden;
}

/* Ensure all text elements use the correct font */
h1, h2, h3, h4, h5, h6, p, span, div, button, input, textarea, select, label {
  font-family: var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  padding: var(--space-2);
}

@media (min-width: 768px) {
  .App {
    padding: var(--space-4);
  }
}

h1 {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  background: linear-gradient(
    90deg,
    var(--gradient-start) 0%,
    var(--gradient-end) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
}

.reward-card {
  width: 311px;
  max-width: 311px;
  flex: 0 0 auto;
}

@media (min-width: 768px) {
  .reward-card {
    width: 311px;
    max-width: 311px;
  }
} 